<template>
  <v-container>
    
    <!-- *********** Logo ************** -->
    <section>
   <v-app-bar
          class="app-bar"
          elevation="4"
          color="#FABFAD"
      >
      <img
        height="30"
        src="../assets/rio-mare-logo.png"
      >
      <v-spacer></v-spacer>

    <router-link to="/">
      <v-btn icon style="color: #01184d">
        <v-icon>mdi-home</v-icon>
      </v-btn>
    </router-link>
      </v-app-bar>
    </section>

<section v-if="loaded && (!resultOK && !resultKO)">
    <!-- *************  Body    *************** -->
    <section class="pr-5 pl-5 mt-15">
      <div style="width: 100%" class="d-flex justify-center">
      <img src="https://via.placeholder.com/150" alt="">
      </div>
      <p class="mt-4">
        Inserisci di seguito le informazioni che leggi sulla confezione
      </p>
    </section>

    <section class="pl-5 pr-5">
     <v-text-field
            label="Codice Lotto"
            placeholder="Inserisci codice lotto"
            outlined
            v-model="codiceLotto"
          ></v-text-field>

        <v-text-field
            label="Linea Produzione"
            placeholder="Inserisci linea produzione"
            outlined
            v-model="lineaProduzione"
          ></v-text-field>

       <v-text-field
            label="Ora Produzione"
            placeholder="Inserisci ora produzione"
            outlined
            v-model="oraProduzione"
          ></v-text-field>

    </section>

    <!--  ******************   Button Footer    ********************* -->
    <section style="position: fixed; bottom: 30px; width: 100%; left: 0;" class="pl-5 pr-5">
      <v-row>
      <v-col cols="12">
        <v-btn
          block
          color="primary"
          elevation="5"
          large
          @click="verifyTraceability"
        >
        Scopri la tracciabilità
        </v-btn>
      </v-col>
    </v-row>
    </section>
  </section>

     <section v-if="!loaded" style="height: 75vh" class="d-flex justify-center align-center">
      <v-progress-circular
      :size="70"
      :width="7"
      color="primary"
      indeterminate
    ></v-progress-circular>

    </section>

      <!-- *********************  Traceability result OK ***************** -->
    <section v-if="resultOK && loaded" style="height: 75vh" class="d-flex justify-center align-center">
      <v-card
         color="success"
         elevation="2"
         class="mx-auto pa-5 mt-10"
         style="position: relative"
      >
        <div style="position: absolute; top: 10px; right: 10px;">
          <v-btn
              icon
              color="white"
               @click="closeAlert"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
        </div>
        <v-card-title class="mt-3">
          <p  class="text-center" style="white-space:pre-wrap; word-break:break-word;color:white !important">
            La tracciabilità del prodotto selezionato è stata verificata con successo
          </p> 
            
        </v-card-title>
      </v-card>

    </section>

    <!-- *********************  Traceability result KO ***************** -->
    <section v-if="resultKO && loaded" style="height: 75vh" class="d-flex justify-center align-center">
      <v-card
         color="error"
         elevation="2"
         class="mx-auto pa-5 mt-10"
         style="position: relative"
      >
        <div style="position: absolute; top: 10px; right: 10px;">
          <v-btn
              icon
              color="white"
              @click="closeAlert"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
        </div>
        <v-card-title class="mt-3">
          <p  class="text-center" style="white-space:pre-wrap; word-break:break-word;color:white !important">
            La tracciabilità del prodotto selezionato non è stata verificata. 
            Ti chiediamo di verificare i dati scansionati e/o inseriti e riprovare 
          </p> 
            
        </v-card-title>
      </v-card>
    </section>
    
  </v-container>
</template>

<script>
import axios from 'axios'
  export default {
    name: 'InsertCodeComponent',
    data () {
      return {
         loaded: true,
         codiceLotto: '',
         lineaProduzione: '',
         oraProduzione: '',
         prod_res: null,
         resultOK: false,
         resultKO: false,
      }
    },
     async mounted () {
       let codeSession = sessionStorage.getItem('product')
        this.prod_res = await axios.get('http://localhost:4000/products/' + codeSession)
     },
     methods: {
         verifyTraceability () {
        this.loaded = false

          axios.post('http://localhost:4000/traceability', {
          product_id: 1,
          description: this.prod_res.data.name,
          weight: this.prod_res.data.weight,
          lot: this.codiceLotto,
          code: this.lineaProduzione,
          production_hours: this.oraProduzione
        })
        .then( () => {
          this.loaded = true
          this.resultOK = true
          this.resultKO = false
        } ) 
        .catch( () => {
          this.loaded = true
          this.resultKO = true
          this.resultOK = false
        })
      },
      closeAlert () {
        this.resultKO = false
        this.resultOK = false
      }
     }
  }
</script>
<style scoped>
  .v-application .primary {
    background-color: #01184d !important;
    border-color: #01184d !important;
}

.v-application a {
    color: #01184d;
}

.v-application .primary--text {
    color: #01184d !important;
    caret-color: #01184d !important;
}
</style>
